.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

header
{
    height: 10vh;
    background-color: #383838;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

header ul
{
    display: flex;
    align-items: center;
}

header h1
{
    background-color: #1f86e7;
    height: 5vh;
    padding: .5vh 1vw 1vh 5vw;
    font-size: 250%;
    color: #383838;
}

header ul li a
{
    color: #1f86e7;
    padding: 1vh;
    font-size: 150%;
    text-decoration-thickness: 3px;
    text-underline-offset: 5px;
}

header ul
{
    margin: 5vw;
}


main section {
    margin: 2vh 5vw 1vh 7vw;
    display: flex;
}

section h1
{
    color: #1f86e7;
    font-size: 200%;
    text-align: right;
    display: block;
    border-right: 2px solid #1f86e7;
    width: 20%;
    padding-right: .7vw;
}


section div p
{
    display: inline-block;
    line-height: 1.2;
    width: 100%;
}

section > div, section ul
{
    width: 100%;
    padding-left: 3vh;
}

main > div
{
    position: relative;
    height: 150px;
}

main > div img
{
    display: flex;
    height: 100%;
    width: 100%;
    align-items: stretch;
    
}

main > div h1
{
    margin-right: 2vw;
    background-color: #1f86e7;
    font-size: 150%;
    padding: 10px;
    position: absolute;
    bottom: 1.5vh;
    right: 0;
}

.project-cards
{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.project-card
{
    margin: 0 1.5vw 1.5vh 0;
    border: 4px solid #1f86e7;
    width: 47%;
    position: relative;
}

#main-card
{
    width: 97%
}

.project-cards img
{
    width: 100%;
    max-width: 100%;
    max-height: 100%;
    flex-shrink: 0;
    filter: grayscale(100%);
    transition: all 0.5s ease;
}

.project-card img:hover
{
    filter: grayscale(0%);
}

.project-card div
{
    position: absolute;
    bottom: 1.5vh;
    left: 0;
    background-color: #1f86e7;
    padding: 1vh;
}


.contact-me
{
    overflow: hidden;
}

.contact-me ul
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.contact-me ul li, .contact-me ul li a
{
    text-decoration: underline;
    text-decoration-color: #445876;
    text-decoration-thickness: 3px;
    text-underline-offset: 5px;
}

footer
{
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 1068px)
{
    main section
    {
        display: block;
    }

    #main-card
    {
        width: 100%;
    }

    .project-cards, .project-card
    {
        width: 100%;
    }

    section h1
    {
        border-right: none;
        border-bottom: 2px solid #1f86e7;
        width: 100%;
        text-align: left;
    }

    footer ul
    {
        display: block;
    }

    header
    {
        display: flex;
    }

    header ul
    {
        display: flex;
    }

    header ul li a
    {
        border: none;
        width: 45%;
    }

    .contact-me ul
    {
        display: block;
    }

    .contact-me ul li
    {
        display: flex;
        justify-content: center;
    }

    .contact-me ul li
    {
        margin: 2vh;
    }
}

@media screen and (max-width: 867px)
{
    header
    {
        display: block;
        height: 100%;
        padding-top: .2vh;
        padding-bottom: .2vh;
    }

    header ul
    {
        width: 80%;
        display: block;
        justify-content: center;
    }

    header ul li a
    {
        display: inline-block;
        width: 90%;
        padding-top: 1vh;
        padding-bottom: 1vh;
    }
}

@media screen and (max-width:500px)
{
    body section ul li > * 
    {
        width: 100%;
    }

    header ul li a 
    {
        width: 100%;
    }
}